/**
 * Rules   
 */

import * as vscode from 'vscode';
import { SlashCommand } from './slashCommand';
import { RulesService } from '../services/rulesService';

/**
 * Rules    
 */
export function createRulesCommands(rulesService: RulesService): SlashCommand[] {
  if (!rulesService) {
    return [];
  }
  
  const commands: SlashCommand[] = [];
  
  // rules : Rules   
  commands.push({
    name: 'rules',
    aliases: ['rule', 'ape-rules', '', '', ''],
    description: 'APE Rules  (, , , , )',
    examples: [
      '/rules list', 
      '/rules active', 
      '/rules inactive', 
      '/rules activate ', 
      '/rules deactivate ',
      '/rules create ',
      '/rules delete ',
      '/rules open ',
      '/rules info'
    ],
    category: 'advanced',
    priority: 15,
    execute: async (context) => {
      const subCommand = context.args[0]?.toLowerCase();

      if (!subCommand) {
        // Rules     (/  )
        const rulesSubcommands = [
          { command: 'list', description: ' Rules  ' },
          { command: 'active', description: ' Rules  ' },
          { command: 'inactive', description: ' Rules  ' },
          { command: 'activate', description: ' Rule ' },
          { command: 'deactivate', description: ' Rule ' },
          { command: 'create', description: ' Rule ' },
          { command: 'delete', description: ' Rule ' },
          { command: 'open', description: ' Rule  ' },
          { command: 'info', description: 'Rules   ' }
        ];

        //      UI 
        const suggestions = rulesSubcommands.map(cmd => ({
          label: `/rules ${cmd.command}`,
          description: cmd.description,
          category: 'advanced',
          insertText: `/rules ${cmd.command} `
        }));

        //    -    UI 
        vscode.commands.executeCommand('ape.showCommandSuggestions', suggestions);

        // VSCode  UI   ( )
        vscode.window.showQuickPick(
          rulesSubcommands.map(cmd => ({
            label: cmd.command,
            description: cmd.description,
            detail: `Rules  : ${cmd.command}`
          })),
          {
            placeHolder: 'Rules  ',
            matchOnDescription: true
          }
        ).then(selected => {
          if (selected) {
            //     
            vscode.commands.executeCommand('ape.insertToChatInput', `/rules ${selected.label}`);
          }
        });
      } else if (subCommand === 'list' || subCommand === '') {
        // Rules  
        await listRules(rulesService);
      } else if (subCommand === 'active' || subCommand === '' || subCommand === '') {
        //  Rules  
        await listActiveRules(rulesService);
      } else if (subCommand === 'inactive' || subCommand === '' || subCommand === '') {
        //  Rules  
        await listInactiveRules(rulesService);
      } else if (subCommand === 'activate' || subCommand === '') {
        // Rule 
        const ruleName = context.args.slice(1).join(' ');
        if (ruleName) {
          await activateRule(rulesService, ruleName);
        } else {
          vscode.window.showErrorMessage(' Rule  ');
        }
      } else if (subCommand === 'deactivate' || subCommand === '') {
        // Rule 
        const ruleName = context.args.slice(1).join(' ');
        if (ruleName) {
          await deactivateRule(rulesService, ruleName);
        } else {
          vscode.window.showErrorMessage(' Rule  ');
        }
      } else if (subCommand === 'create' || subCommand === '' || subCommand === '') {
        // Rule 
        const ruleName = context.args.slice(1).join(' ');
        if (ruleName) {
          await createRule(rulesService, ruleName);
        } else {
          vscode.window.showErrorMessage(' Rule  ');
        }
      } else if (subCommand === 'delete' || subCommand === '') {
        // Rule 
        const ruleName = context.args.slice(1).join(' ');
        if (ruleName) {
          await deleteRule(rulesService, ruleName);
        } else {
          vscode.window.showErrorMessage(' Rule  ');
        }
      } else if (subCommand === 'open' || subCommand === '') {
        // Rule  
        const ruleName = context.args.slice(1).join(' ');
        if (ruleName) {
          await openRuleFile(rulesService, ruleName);
        } else {
          vscode.window.showErrorMessage(' Rule  ');
        }
      } else if (subCommand === 'info' || subCommand === '') {
        // Rules  
        await showRulesInfo(rulesService);
      } else {
        vscode.window.showErrorMessage(`   Rules  : ${subCommand}`);
      }
    },
    provideCompletions: (partialArgs: string) => {
      const subCommands = [
        'list', 'active', 'inactive', 'activate', 'deactivate', 'create', 'delete', 'open', 'info',
        '', '', '', '', '', '', '', '', '', ''
      ];
      
      const parts = partialArgs.split(' ');
      
      //     ( )
      if (parts.length <= 1) {
        return subCommands.filter(cmd => 
          cmd.toLowerCase().startsWith(parts[0]?.toLowerCase() || '')
        );
      }
      
      //     (Rule )
      if (parts.length === 2 && ['activate', 'deactivate', 'delete', 'open', '', '', '', ''].includes(parts[0])) {
        //    
        //  / Rule      
        return [];
      }
      
      return [];
    }
  });
  
  
  return commands;
}

/**
 * Rules  
 */
async function listRules(rulesService: RulesService): Promise<void> {
  try {
    const rules = rulesService.getAllRules();
    
    if (rules.length === 0) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: ' Rule . `/rules create `  Rule .'
      });
      return;
    }
    
    let content = '## APE Rules \n\n';
    
    //  Rules  
    const activeRules = rules.filter(rule => rule.status === 'active');
    const inactiveRules = rules.filter(rule => rule.status === 'inactive');
    
    if (activeRules.length > 0) {
      content += '###   Rules\n\n';
      activeRules.forEach(rule => {
        content += `- **${rule.name}** - \`${rule.id}.md\`\n`;
      });
      content += '\n';
    }
    
    if (inactiveRules.length > 0) {
      content += '###   Rules\n\n';
      inactiveRules.forEach(rule => {
        content += `- **${rule.name}** - \`${rule.id}.md\`\n`;
      });
      content += '\n';
    }
    
    content += '\n**Rules  **:\n';
    content += '- `/rules activate ` - Rule \n';
    content += '- `/rules deactivate ` - Rule \n';
    content += '- `/rules create ` -  Rule \n';
    content += '- `/rules delete ` - Rule \n';
    content += '- `/rules open ` - Rule  \n';
    
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content
    });
  } catch (error) {
    vscode.window.showErrorMessage(`Rules     : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *  Rules  
 */
async function listActiveRules(rulesService: RulesService): Promise<void> {
  try {
    const activeRules = rulesService.getActiveRules();
    
    if (activeRules.length === 0) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: '  Rule . `/rules activate ` Rule .'
      });
      return;
    }
    
    let content = '##   APE Rules\n\n';
    
    activeRules.forEach(rule => {
      //  100  
      const previewContent = rule.content.length > 100 
        ? rule.content.substring(0, 100) + '...' 
        : rule.content;
      
      content += `### ${rule.name}\n\n`;
      content += `- **ID**: \`${rule.id}\`\n`;
      content += `- ****: \`${rule.filePath.split('/').pop()}\`\n`;
      content += `- ** **:\n\n\`\`\`\n${previewContent}\n\`\`\`\n\n`;
    });
    
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content
    });
  } catch (error) {
    vscode.window.showErrorMessage(` Rules     : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *  Rules  
 */
async function listInactiveRules(rulesService: RulesService): Promise<void> {
  try {
    const allRules = rulesService.getAllRules();
    const activeRules = rulesService.getActiveRules();
    
    const inactiveRules = allRules.filter(rule => 
      !activeRules.some(activeRule => activeRule.id === rule.id)
    );
    
    if (inactiveRules.length === 0) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: '  Rule .  Rules  .'
      });
      return;
    }
    
    let content = '##   APE Rules\n\n';
    
    inactiveRules.forEach(rule => {
      //  100  
      const previewContent = rule.content.length > 100 
        ? rule.content.substring(0, 100) + '...' 
        : rule.content;
      
      content += `### ${rule.name}\n\n`;
      content += `- **ID**: \`${rule.id}\`\n`;
      content += `- ****: \`${rule.filePath.split('/').pop()}\`\n`;
      content += `- ** **:\n\n\`\`\`\n${previewContent}\n\`\`\`\n\n`;
    });
    
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content
    });
  } catch (error) {
    vscode.window.showErrorMessage(` Rules     : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 * Rule 
 */
async function activateRule(rulesService: RulesService, ruleName: string): Promise<void> {
  try {
    const rules = rulesService.getAllRules();
    
    //   ID Rule 
    const rule = rules.find(r => 
      r.name.toLowerCase() === ruleName.toLowerCase() || 
      r.id.toLowerCase() === ruleName.toLowerCase()
    );
    
    if (!rule) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `'${ruleName}'   ID  Rule   . '/rules list'   Rules .`
      });
      return;
    }
    
    //   
    if (rule.status === 'active') {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `'${rule.name}' Rule   .`
      });
      return;
    }
    
    // Rule 
    await rulesService.activateRule(rule.id);
    
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `'${rule.name}' Rule .  LLM   .`
    });
  } catch (error) {
    vscode.window.showErrorMessage(`Rule    : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 * Rule 
 */
async function deactivateRule(rulesService: RulesService, ruleName: string): Promise<void> {
  try {
    const rules = rulesService.getAllRules();
    
    //   ID Rule 
    const rule = rules.find(r => 
      r.name.toLowerCase() === ruleName.toLowerCase() || 
      r.id.toLowerCase() === ruleName.toLowerCase()
    );
    
    if (!rule) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `'${ruleName}'   ID  Rule   . '/rules list'   Rules .`
      });
      return;
    }
    
    //   
    if (rule.status === 'inactive') {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `'${rule.name}' Rule   .`
      });
      return;
    }
    
    // Rule 
    await rulesService.deactivateRule(rule.id);
    
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `'${rule.name}' Rule .   LLM    .`
    });
  } catch (error) {
    vscode.window.showErrorMessage(`Rule    : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *  Rule 
 */
async function createRule(rulesService: RulesService, ruleName: string): Promise<void> {
  try {
    // Rule  
    const ruleContent = `# ${ruleName}\n\n LLM    .\n\n   .\n\n## \n\n1.    .\n2.   .\n3.    .`;
    
    // Rule 
    const rule = await rulesService.createRule(ruleName, ruleContent, false);
    
    // Rule  
    await rulesService.openRuleFile(rule.id);
    
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `'${rule.name}' Rule .    , '/rules activate ${rule.name}'   .`
    });
  } catch (error) {
    vscode.window.showErrorMessage(`Rule    : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 * Rule 
 */
async function deleteRule(rulesService: RulesService, ruleName: string): Promise<void> {
  try {
    const rules = rulesService.getAllRules();
    
    //   ID Rule 
    const rule = rules.find(r => 
      r.name.toLowerCase() === ruleName.toLowerCase() || 
      r.id.toLowerCase() === ruleName.toLowerCase()
    );
    
    if (!rule) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `'${ruleName}'   ID  Rule   . '/rules list'   Rules .`
      });
      return;
    }
    
    //   (  )
    const confirmation = await vscode.window.showWarningMessage(
      `'${rule.name}' Rule ?`,
      { modal: true },
      '',
      ''
    );
    
    if (confirmation !== '') {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `'${rule.name}' Rule  .`
      });
      return;
    }
    
    // Rule 
    await rulesService.deleteRule(rule.id);
    
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `'${rule.name}' Rule .`
    });
  } catch (error) {
    vscode.window.showErrorMessage(`Rule    : ${error instanceof Error ? error.message : String(error)}`);
  }
}

