/**
 *  
 * 
 *   UI    ,     
 */

import * as vscode from 'vscode';
import * as fs from 'fs';
import * as path from 'path';

/**
 *    
 */
export interface CodeInsertionOptions {
  code: string;
  language: string;
  replaceSelection?: boolean;
  insertAtCursor?: boolean;
  createNewFile?: boolean;
  filename?: string;
}

/**
 *    
 */
export class CodeService {
  private static codeBlockCounter = 0;
  
  /**
   *    
   * @param context  
   * @param webview 
   */
  public static async registerHandlers(context: vscode.ExtensionContext, webview: vscode.Webview): Promise<void> {
    console.log('CodeService.registerHandlers called');
    
    //      (  )
    await this.registerCommands(context);
    
    //    
    webview.onDidReceiveMessage(
      async (message) => {
        switch (message.type) {
          case 'copyCode': {
            await this.handleCopyCode(message.code);
            break;
          }
            
          case 'insertCodeToEditor': {
            await this.handleInsertCode(message);
            break;
          }
            
          case 'createFileWithCode': {
            await this.handleCreateFile(message);
            break;
          }
            
          case 'attachFile': {
            await this.handleAttachFile(webview);
            break;
          }
        }
      },
      undefined,
      context.subscriptions
    );
  }
  
  /**
   *   
   * @param code  
   */
  private static async handleCopyCode(code: string): Promise<void> {
    try {
      await vscode.env.clipboard.writeText(code);
      vscode.window.showInformationMessage('  .');
    } catch (error) {
      console.error('    :', error);
      vscode.window.showErrorMessage('    .');
    }
  }
  
  /**
   *   
   * @param message  
   */
  private static async handleInsertCode(message: any): Promise<void> {
    try {
      const options: CodeInsertionOptions = {
        code: message.code,
        language: message.language || 'plaintext',
        insertAtCursor: message.insertAtCursor,
        replaceSelection: message.replaceSelection,
        createNewFile: message.createNewFile,
        filename: message.filename
      };
      
      const result = await vscode.commands.executeCommand('ape.insertCodeToEditor', options);
      
      if (!result) {
        vscode.window.showErrorMessage('   .');
      }
    } catch (error) {
      console.error('    :', error);
      vscode.window.showErrorMessage('    .');
    }
  }
  
  /**
   *    
   * @param message  
   */
  private static async handleCreateFile(message: any): Promise<void> {
    try {
      const options: CodeInsertionOptions = {
        code: message.code,
        language: message.language || 'plaintext',
        createNewFile: true,
        filename: message.filename
      };
      
      const result = await vscode.commands.executeCommand('ape.createNewFileWithCode', options);
      
      if (!result) {
        vscode.window.showErrorMessage('   .');
      }
    } catch (error) {
      console.error('    :', error);
      vscode.window.showErrorMessage('    .');
    }
  }
  
  /**
   *   
   * @param webview 
   */
  private static async handleAttachFile(webview: vscode.Webview): Promise<void> {
    try {
      // VSCode    
      const fileUris = await vscode.window.showOpenDialog({
        canSelectFiles: true,
        canSelectFolders: false,
        canSelectMany: false,
        openLabel: '',
        filters: {
          'All Files': ['*']
        }
      });

      if (!fileUris || fileUris.length === 0) {
        return; //  
      }

      const fileUri = fileUris[0];
      const fileName = path.basename(fileUri.fsPath);
      const fileExtension = path.extname(fileUri.fsPath).toLowerCase();
      let fileContent = '';

      //    (    )
      const stat = await vscode.workspace.fs.stat(fileUri);
      const fileSize = stat.size;
      const MAX_FILE_SIZE = 5 * 1024 * 1024; // 5MB

      //     
      if (fileSize <= MAX_FILE_SIZE) {
        try {
          const buffer = await vscode.workspace.fs.readFile(fileUri);
          fileContent = new TextDecoder().decode(buffer);
        } catch (error) {
          console.log('    :', error);
          //      
        }
      }

      //   
      let relativePath = fileUri.fsPath;
      if (vscode.workspace.workspaceFolders?.length) {
        const workspaceFolder = vscode.workspace.workspaceFolders[0];
        const workspacePath = workspaceFolder.uri.fsPath;
        if (fileUri.fsPath.startsWith(workspacePath)) {
          relativePath = fileUri.fsPath.substring(workspacePath.length + 1);
        }
      }

      //    
      webview.postMessage({
        type: 'fileAttached',
        file: {
          path: fileUri.fsPath,
          relativePath: relativePath,
          name: fileName,
          type: fileExtension,
          size: fileSize,
          content: fileContent,
          hasContent: fileContent.length > 0
        }
      });

      vscode.window.showInformationMessage(`${fileName}  .`);
    } catch (error) {
      console.error('    :', error);
      vscode.window.showErrorMessage(`    : ${error instanceof Error ? error.message : String(error)}`);
    }
  }

  /**
   *   CSS  
   * @param webview 
   * @param context  
   * @returns CSS URI
   */
  public static getCodeBlockStyleUri(webview: vscode.Webview, context: vscode.ExtensionContext): vscode.Uri {
    return webview.asWebviewUri(
      vscode.Uri.joinPath(context.extensionUri, 'media', 'code-block.css')
    );
  }
  
  /**
   *    
   * @returns JavaScript 
   */
  public static getCodeBlockScript(): string {
    return `
    //    
    (function() {
      document.addEventListener('click', function(event) {
        const target = event.target;
        
        //    (    )
        const copyButton = target.closest('.code-action-button.copy-button');
        if (copyButton) {
          const codeId = copyButton.getAttribute('data-code-id');
          const codeElement = document.getElementById('code-' + codeId);
          
          if (codeElement) {
            const code = codeElement.textContent;
            
            // VS Code   
            vscode.postMessage({
              type: 'copyCode',
              code: code,
              language: codeElement.className.replace('language-', '')
            });
            
            //    
            const codeBlock = copyButton.closest('.code-block-container');
            codeBlock.classList.add('success-animation');
            setTimeout(() => {
              codeBlock.classList.remove('success-animation');
            }, 1000);
          }
          
          event.preventDefault();
          return;
        }
        
        //    
        const insertButton = target.closest('.code-action-button.insert-code-button');
        if (insertButton) {
          const codeId = insertButton.getAttribute('data-code-id');
          const codeElement = document.getElementById('code-' + codeId);
          
          if (codeElement) {
            const code = codeElement.textContent;
            const language = codeElement.className.replace('language-', '');
            
            // VS Code   
            vscode.postMessage({
              type: 'insertCodeToEditor',
              code: code,
              language: language,
              insertAtCursor: true
            });
          }
          
          event.preventDefault();
          return;
        }
        
        //     
        const newFileButton = target.closest('.code-action-button.new-file-button');
        if (newFileButton) {
          const codeId = newFileButton.getAttribute('data-code-id');
          const codeElement = document.getElementById('code-' + codeId);
          
          if (codeElement) {
            const code = codeElement.textContent;
            const language = codeElement.className.replace('language-', '');
            
            // VS Code     
            vscode.postMessage({
              type: 'createFileWithCode',
              code: code,
              language: language
            });
          }
          
          event.preventDefault();
          return;
        }
      });
      
      //     
      function markStreamingCodeBlock(id, isStreaming) {
        const codeBlock = document.getElementById('code-' + id)?.closest('.code-block-container');
        if (codeBlock) {
          if (isStreaming) {
            codeBlock.classList.add('streaming-code-block');
          } else {
            codeBlock.classList.remove('streaming-code-block');
          }
        }
      }
      
      //  /  
      window.addEventListener('message', (event) => {
        const message = event.data;
        if (message.type === 'streamingCodeStart') {
          markStreamingCodeBlock(message.codeId, true);
        } 
        else if (message.type === 'streamingCodeEnd') {
          markStreamingCodeBlock(message.codeId, false);
        }
      });
    })();
    `;
  }
  
  /**
   *    
   */
  public static async getCodeBlockTemplate(): Promise<string> {
    const extensionPath = vscode.extensions.getExtension('ape.ape-extension')?.extensionPath;
    
    if (!extensionPath) {
      console.error('     .');
      return this.getDefaultCodeBlockTemplate();
    }
    
    const templatePath = path.join(extensionPath, 'src', 'data', 'codeBlockTemplate.html');
    
    try {
      return await fs.promises.readFile(templatePath, 'utf-8');
    } catch (error) {
      console.error('     :', error);
      return this.getDefaultCodeBlockTemplate();
    }
  }
  
  /**
   *     
   */
  private static getDefaultCodeBlockTemplate(): string {
    return '<div class="code-block-container code-block-popup">\n' +
    '  <div class="code-block-header">\n' +
    '    <span class="code-block-language language-${language}">${language}</span>\n' +
    '    <div class="code-block-actions">\n' +
    '      <button class="code-action-button copy-button" data-code-id="${codeId}" title="">\n' +
    '        <i class="codicon codicon-copy"></i>\n' +
    '        <span class="tooltip"> </span>\n' +
    '      </button>\n' +
    '      <button class="code-action-button insert-code-button" data-code-id="${codeId}" title=" ">\n' +
    '        <i class="codicon codicon-arrow-small-right"></i>\n' +
    '        <span class="tooltip">  </span>\n' +
    '      </button>\n' +
    '      <button class="code-action-button new-file-button" data-code-id="${codeId}" title="  ">\n' +
    '        <i class="codicon codicon-new-file"></i>\n' +
    '        <span class="tooltip">  </span>\n' +
    '      </button>\n' +
    '    </div>\n' +
    '  </div>\n' +
    '  <div class="code-content ${showLineNumbers ? \'with-line-numbers\' : \'\'}">\n' +
    '    ${showLineNumbers ? \'<div class="line-numbers">${lineNumbers}</div>\' : \'\'}\n' +
    '    <div class="code-area">\n' +
    '      <code class="language-${language}" id="code-${codeId}">${codeContent}</code>\n' +
    '    </div>\n' +
    '  </div>\n' +
    '</div>';
  }

  /**
   *     
   * @param options   
   * @returns  
   */
  public static async insertCodeToEditor(options: CodeInsertionOptions): Promise<boolean> {
    try {
      const { code, replaceSelection, insertAtCursor, createNewFile, filename } = options;
      
      //      
      if (createNewFile) {
        return await this.createNewFileWithCode(code, options.language, filename);
      }
      
      //    
      const editor = vscode.window.activeTextEditor;
      if (!editor) {
        //    ,    
        const createNewFile = await vscode.window.showInformationMessage(
          '  .   ?',
          '  ',
          ''
        );
        
        if (createNewFile === '  ') {
          return await this.createNewFileWithCode(code, options.language);
        }
        
        return false;
      }
      
      //    
      const selection = editor.selection;
      
      //   
      await editor.edit(editBuilder => {
        if (replaceSelection && !selection.isEmpty) {
          //   
          editBuilder.replace(selection, code);
        } else if (insertAtCursor) {
          //   
          editBuilder.insert(selection.active, code);
        } else {
          //       
          editBuilder.insert(selection.active, code);
        }
      });
      
      //    (    )
      try {
        await vscode.commands.executeCommand('editor.action.formatDocument');
      } catch (error) {
        console.log('    :', error);
        //    (    )
      }
      
      return true;
    } catch (error) {
      console.error('    :', error);
      vscode.window.showErrorMessage(`    : ${error instanceof Error ? error.message : String(error)}`);
      return false;
    }
  }
  
  /**
   *    
   * @param code  
   * @param language 
   * @param suggestedFilename  
   * @returns  
   */
  private static async createNewFileWithCode(code: string, language: string, suggestedFilename?: string): Promise<boolean> {
    try {
      //   
      const extension = this.getFileExtensionForLanguage(language);
      const filename = suggestedFilename || `new_file${extension}`;
      
      //   
      const document = await vscode.workspace.openTextDocument({
        language: language,
        content: code
      });
      
      await vscode.window.showTextDocument(document);
      
      //    
      const saveFile = await vscode.window.showInformationMessage(
        '  . ?',
        '',
        ''
      );
      
      if (saveFile === '') {
        if (document.isDirty) {
          const uri = await vscode.window.showSaveDialog({
            defaultUri: vscode.Uri.file(filename),
            filters: {
              ' ': ['*']
            }
          });
          
          if (uri) {
            await vscode.workspace.fs.writeFile(uri, Buffer.from(code));
            vscode.window.showInformationMessage(` : ${uri.fsPath}`);
          }
        }
      }
      
      return true;
    } catch (error) {
      console.error('     :', error);
      vscode.window.showErrorMessage(`     : ${error instanceof Error ? error.message : String(error)}`);
      return false;
    }
  }
  
  /**
   *  ID   
   * @param language  ID
   * @returns  
   */
  private static getFileExtensionForLanguage(language: string): string {
    const languageToExtension: Record<string, string> = {
      'typescript': '.ts',
      'javascript': '.js',
      'python': '.py',
      'java': '.java',
      'csharp': '.cs',
      'c': '.c',
      'cpp': '.cpp',
      'go': '.go',
      'rust': '.rs',
      'php': '.php',
      'ruby': '.rb',
      'html': '.html',
      'css': '.css',
      'json': '.json',
      'markdown': '.md',
      'plaintext': '.txt',
      'xml': '.xml',
      'yaml': '.yml',
      'shell': '.sh',
      'bash': '.sh',
      'powershell': '.ps1',
      'sql': '.sql'
    };
    
    return languageToExtension[language] || '.txt';
  }
  
  /**
   *       
   * @returns  
   */
  public static async promptForInsertionMode(): Promise<Partial<CodeInsertionOptions> | undefined> {
    const options = await vscode.window.showQuickPick([
      {
        label: '$(cursor)   ',
        description: '    .',
        detail: '   .',
        value: 'insert'
      },
      {
        label: '$(edit)   ',
        description: '    .',
        detail: '     .',
        value: 'replace'
      },
      {
        label: '$(new-file)   ',
        description: '   .',
        detail: '     .',
        value: 'new'
      }
    ], {
      placeHolder: '   ',
      matchOnDescription: true,
      matchOnDetail: true
    });
    
    if (!options) {
      return undefined; //  
    }
    
    switch (options.value) {
      case 'insert': {
        return { insertAtCursor: true };
      }
      case 'replace': {
        return { replaceSelection: true };
      }
      case 'new': {
        return { createNewFile: true };
      }
    }
    
    return undefined;
  }
  
  /**
   *    
   * @param context  
   */
  public static async registerCommands(context: vscode.ExtensionContext): Promise<void> {
    //       
    const existingCommands = await vscode.commands.getCommands();
    
    //     ( )
    if (!existingCommands.includes('ape.insertCodeToEditor')) {
      context.subscriptions.push(
        vscode.commands.registerCommand('ape.insertCodeToEditor', async (options: CodeInsertionOptions) => {
          const insertionOptions = await this.promptForInsertionMode();
          if (!insertionOptions) {
            return false;
          }
          
          return await this.insertCodeToEditor({
            ...options,
            ...insertionOptions
          });
        })
      );
    }
    
    //    (  )
    if (!existingCommands.includes('ape.insertCodeAtCursor')) {
      context.subscriptions.push(
        vscode.commands.registerCommand('ape.insertCodeAtCursor', async (options: CodeInsertionOptions) => {
          return await this.insertCodeToEditor({
            ...options,
            insertAtCursor: true
          });
        })
      );
    }
    
    //    
    if (!existingCommands.includes('ape.replaceSelectionWithCode')) {
      context.subscriptions.push(
        vscode.commands.registerCommand('ape.replaceSelectionWithCode', async (options: CodeInsertionOptions) => {
          return await this.insertCodeToEditor({
            ...options,
            replaceSelection: true
          });
        })
      );
    }
    
    //    
    if (!existingCommands.includes('ape.createNewFileWithCode')) {
      context.subscriptions.push(
        vscode.commands.registerCommand('ape.createNewFileWithCode', async (options: CodeInsertionOptions) => {
          return await this.insertCodeToEditor({
            ...options,
            createNewFile: true
          });
        })
      );
    }
    
    //   
    if (!existingCommands.includes('ape.attachFile')) {
      context.subscriptions.push(
        vscode.commands.registerCommand('ape.attachFile', async () => {
          try {
            //   
            const activeViewColumn = vscode.window.activeTextEditor?.viewColumn || vscode.ViewColumn.One;
            const panel = vscode.window.visibleTextEditors
              .find(editor => editor.viewColumn === activeViewColumn);
            
            if (panel) {
              //    
              const fileUris = await vscode.window.showOpenDialog({
                canSelectFiles: true,
                canSelectFolders: false,
                canSelectMany: false,
                openLabel: '',
                filters: {
                  'All Files': ['*']
                }
              });

              if (fileUris && fileUris.length > 0) {
                return fileUris[0].fsPath;
              }
            }
            return null;
          } catch (error) {
            console.error('   :', error);
            vscode.window.showErrorMessage(`    : ${error instanceof Error ? error.message : String(error)}`);
            return null;
          }
        })
      );
    }
  }

  /**
   *   UI 
   * @param codeContent  
   * @param language 
   * @param showLineNumbers    
   * @returns  HTML
   */
  public static formatCodeBlock(
    codeContent: string,
    language: string = 'plaintext',
    showLineNumbers: boolean = true
  ): string {
    const codeId = `code_${++this.codeBlockCounter}`;
    const escapedCode = this.escapeHtml(codeContent);
    
    //   
    const lineNumbers = showLineNumbers ? 
      codeContent.split('\n').map((_, i) => `${i + 1}`).join('\n') : '';
    
    //    
    const template = `<div class="code-block-container code-block-popup">
  <div class="code-block-header">
    <span class="code-block-language language-${language}">${language}</span>
    <div class="code-block-actions">
      <button class="code-action-button copy-button" data-code-id="${codeId}" title="">
        <i class="codicon codicon-copy"></i>
        <span class="tooltip"> </span>
      </button>
      <button class="code-action-button insert-code-button" data-code-id="${codeId}" title=" ">
        <i class="codicon codicon-arrow-small-right"></i>
        <span class="tooltip">  </span>
      </button>
      <button class="code-action-button new-file-button" data-code-id="${codeId}" title="  ">
        <i class="codicon codicon-new-file"></i>
        <span class="tooltip">  </span>
      </button>
    </div>
  </div>
  <div class="code-content ${showLineNumbers ? 'with-line-numbers' : ''}">
    ${showLineNumbers ? `<div class="line-numbers">${lineNumbers}</div>` : ''}
    <div class="code-area">
      <code class="language-${language}" id="code-${codeId}">${escapedCode}</code>
    </div>
  </div>
</div>`;

    return template;
  }

  /**
   * HTML  
   * @param unsafe  
   * @returns  
   */
  private static escapeHtml(unsafe: string): string {
    return unsafe
      .replace(/&/g, "&amp;")
      .replace(/</g, "&lt;")
      .replace(/>/g, "&gt;")
      .replace(/"/g, "&quot;")
      .replace(/'/g, "&#039;");
  }
}