import { IServiceError } from './serviceError';

/**
 *    
 * 
 * @template T   
 */
export interface ServiceResult<T> {
  /**   */
  success: boolean;
  
  /**   ( ) */
  data?: T;
  
  /**   ( ) */
  error?: IServiceError;
}

/**
 *     
 * 
 * @param data  
 * @returns   
 */
export function createSuccessResult<T>(data: T): ServiceResult<T> {
  return {
    success: true,
    data
  };
}

/**
 *     
 * 
 * @param error  
 * @returns   
 */
export function createErrorResult<T>(error: IServiceError): ServiceResult<T> {
  return {
    success: false,
    error
  };
}