/**
 * Message roles representing different participants in a conversation
 */
export enum MessageRole {
  User = 'user',           // Messages from the user
  Assistant = 'assistant', // Messages from the AI assistant
  System = 'system'        // System messages (e.g., errors, notifications)
}

/**
 * Supported LLM models
 */
export enum LLMModel {
  // OpenAI models ( )
  GPT_4_1_MINI = 'openai/gpt-4.1-mini',            //  
  GPT_4_1_PREVIEW = 'openai/gpt-4.1-preview',      //  
  GPT_4O = 'openai/gpt-4o',                        // 
  GPT_3_5_TURBO = 'openai/gpt-3.5-turbo',          //  
  
  // Anthropic models (Claude )
  CLAUDE_3_OPUS = 'anthropic/claude-3-opus-20240229',     //   
  CLAUDE_3_SONNET = 'anthropic/claude-3-sonnet-20240229', //  
  CLAUDE_3_HAIKU = 'anthropic/claude-3-haiku-20240307',   //   
  
  //  
  GEMINI_PRO = 'google/gemini-pro',                // Google  
  GEMMA_7B = 'google/gemma-7b-it',                 //   
  QWEN_72B = 'qwen/qwen-72b-chat',                 // Alibaba  
  DEEPSEEK = 'deepseek/deepseek-coder',            //   
  
  //  
  MISTRAL_7B = 'mistralai/mistral-7b-instruct',    //   
  LLAMA3_8B = 'meta-llama/llama-3-8b-instruct'     //   
}

/**
 * Represents a chat message
 */
export interface Message {
  /** Unique identifier for the message */
  id: string;
  
  /** Role of the message sender */
  role: MessageRole;
  
  /** Message content */
  content: string;
  
  /** Timestamp when the message was created */
  timestamp: Date;
  
  /** Additional message metadata (optional) */
  metadata?: MessageMetadata;
}

/**
 * Optional metadata for messages
 */
export interface MessageMetadata {
  /** Related file paths */
  relatedFiles?: string[];
  
  /** Attached files data */
  attachedFiles?: {
    /** File path within workspace */
    path: string;
    /** File name */
    name: string;
    /** File type/extension */
    type: string;
    /** Content if available (optional for large files) */
    content?: string;
  }[];
  
  /** Token count information */
  tokens?: {
    prompt?: number;
    completion?: number;
    total?: number;
  };
  
  /** Model used for this message */
  model?: string;
  
  /** Additional custom metadata */
  [key: string]: any;
}

/**
 * Request to the LLM API
 */
export interface LLMRequest {
  /** Messages to send to the LLM */
  messages: Message[];
  
  /** Model to use for this request */
  model: string;
  
  /** Temperature for response generation */
  temperature?: number;
  
  /** Maximum tokens to generate */
  maxTokens?: number;
  
  /** Whether to stream the response */
  stream?: boolean;
  
  /** Additional request parameters */
  parameters?: Record<string, any>;
}

/**
 * Response from the LLM API
 */
export interface LLMResponse {
  /** Generated message */
  message: Message;
  
  /** Token usage information */
  usage?: {
    promptTokens: number;
    completionTokens: number;
    totalTokens: number;
  };
  
  /** Additional response metadata */
  metadata?: Record<string, any>;
  
  /** Raw content (for compatibility) */
  content?: string;
}

/**
 * Represents a chat session
 */
export interface ChatSession {
  /** Unique identifier for the session */
  id: string;
  
  /** Human-readable name for the session */
  name: string;
  
  /** When the session was created */
  createdAt: Date;
  
  /** When the session was last updated */
  updatedAt: Date;
  
  /** Messages in this session */
  messages: Message[];
  
  /** Additional session metadata */
  metadata?: {
    /** Related project or workspace */
    project?: string;
    
    /** Session summary */
    summary?: string;
    
    /** Default model for this session */
    defaultModel?: string;
    
    /** Custom session data */
    [key: string]: any;
  };
}

/**
 * Options for LLM API requests
 */
export interface LLMRequestOptions {
  /** Model to use */
  model?: string;
  
  /** Temperature for response generation */
  temperature?: number;
  
  /** Maximum tokens to generate */
  maxTokens?: number;
  
  /** Whether to stream the response */
  stream?: boolean;
  
  /** System prompt to use */
  systemPrompt?: string;
  
  /** Additional context messages */
  contextMessages?: Message[];
  
  /** Additional model parameters */
  modelParameters?: Record<string, any>;
}

/**
 * Callback for streaming responses
 */
export type StreamCallback = (chunk: string, done: boolean) => void;