/**
 * Todo   
 */

import * as vscode from 'vscode';
import { SlashCommand } from './slashCommand';
import { TodoService } from '../services/todoService';
import { TodoPriority, TodoStatus } from '../../types/todo';

/**
 * Todo  
 */
export function createTodoCommands(todoService?: TodoService): SlashCommand[] {
  if (!todoService) {
    return [];
  }
  
  const commands: SlashCommand[] = [];

  // todo :     
  commands.push({
    name: 'todo',
    aliases: ['todos', 'task', 'tasks', '', '', '', ''],
    description: '    (, , , )',
    examples: [
      '/todo list', 
      '/todo add   ',
      '/todo update ID ',
      '/todo status ID completed',
      '/todo priority ID high',
      '/todo delete ID'
    ],
    category: 'utility',
    priority: 6,
    execute: async (context) => {
      const subCommand = context.args[0]?.toLowerCase();
      
      if (!subCommand || subCommand === 'list' || subCommand === '') {
        //    
        await listTodos(todoService);
      } else if (subCommand === 'add' || subCommand === '' || subCommand === '') {
        //   
        const title = context.args.slice(1).join(' ');
        if (title) {
          await addTodo(todoService, title);
        } else {
          vscode.window.showErrorMessage('    ');
        }
      } else if (subCommand === 'update' || subCommand === '' || subCommand === '') {
        //   
        const todoId = context.args[1];
        const updateContent = context.args.slice(2).join(' ');
        
        if (!todoId) {
          vscode.window.showErrorMessage('   ID ');
          return;
        }
        
        if (!updateContent) {
          vscode.window.showErrorMessage('  ');
          return;
        }
        
        await updateTodo(todoService, todoId, updateContent);
      } else if (subCommand === 'status' || subCommand === '') {
        //    
        const todoId = context.args[1];
        const statusStr = context.args[2]?.toLowerCase();
        
        if (!todoId) {
          vscode.window.showErrorMessage('    ID ');
          return;
        }
        
        if (!statusStr) {
          vscode.window.showErrorMessage('   (pending, in-progress, completed, cancelled)');
          return;
        }
        
        await changeTodoStatus(todoService, todoId, statusStr);
      } else if (subCommand === 'priority' || subCommand === '') {
        //    
        const todoId = context.args[1];
        const priorityStr = context.args[2]?.toLowerCase();
        
        if (!todoId) {
          vscode.window.showErrorMessage('    ID ');
          return;
        }
        
        if (!priorityStr) {
          vscode.window.showErrorMessage('   (high, medium, low)');
          return;
        }
        
        await changeTodoPriority(todoService, todoId, priorityStr);
      } else if (subCommand === 'delete' || subCommand === '') {
        //   
        const todoId = context.args[1];
        
        if (!todoId) {
          vscode.window.showErrorMessage('   ID ');
          return;
        }
        
        await deleteTodo(todoService, todoId);
      } else if (subCommand === 'clear' || subCommand === '' || subCommand === '') {
        //     
        await clearAllTodos(todoService);
      } else {
        vscode.window.showErrorMessage(`      : ${subCommand}`);
      }
    },
    provideCompletions: (partialArgs: string) => {
      const subCommands = [
        'list', 'add', 'update', 'status', 'priority', 'delete', 'clear',
        '', '', '', '', '', '', ''
      ];
      
      const parts = partialArgs.split(' ');
      
      //     ( )
      if (parts.length <= 1) {
        return subCommands.filter(cmd => 
          cmd.toLowerCase().startsWith(parts[0]?.toLowerCase() || '')
        );
      }
      
      //     (  )
      if (parts.length === 3) {
        const subCommand = parts[0].toLowerCase();
        
        if (subCommand === 'status' || subCommand === '') {
          const statusValues = ['pending', 'in-progress', 'completed', 'cancelled', '', '', '', ''];
          return statusValues.filter(status => 
            status.toLowerCase().startsWith(parts[2].toLowerCase() || '')
          );
        } else if (subCommand === 'priority' || subCommand === '') {
          const priorityValues = ['high', 'medium', 'low', '', '', ''];
          return priorityValues.filter(priority => 
            priority.toLowerCase().startsWith(parts[2].toLowerCase() || '')
          );
        }
      }
      
      return [];
    }
  });

  return commands;
}

/**
 *    
 */
async function listTodos(todoService: TodoService): Promise<void> {
  try {
    const todoList = todoService.getTodoList();
    
    if (todoList.items.length === 0) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: '    . `/todo add `    .'
      });
      return;
    }
    
    let content = `##   \n\n`;
    content += `- ** **: ${todoList.pendingCount}\n`;
    content += `- ****: ${todoList.completedCount}\n\n`;
    
    //  
    const pending = todoList.items.filter(item => item.status === TodoStatus.PENDING);
    const inProgress = todoList.items.filter(item => item.status === TodoStatus.IN_PROGRESS);
    const completed = todoList.items.filter(item => item.status === TodoStatus.COMPLETED);
    const cancelled = todoList.items.filter(item => item.status === TodoStatus.CANCELLED);
    
    //   
    const getPriorityIcon = (priority: TodoPriority): string => {
      switch (priority) {
        case TodoPriority.HIGH: return '';
        case TodoPriority.MEDIUM: return '';
        case TodoPriority.LOW: return '';
        default: return '';
      }
    };
    
    //   
    if (pending.length > 0) {
      content += `###   \n\n`;
      pending.forEach(item => {
        content += `- ${getPriorityIcon(item.priority)} **${item.title}** (ID: \`${item.id}\`)`;
        if (item.description) {
          content += ` - ${item.description}`;
        }
        content += '\n';
      });
      content += '\n';
    }
    
    //   
    if (inProgress.length > 0) {
      content += `###   \n\n`;
      inProgress.forEach(item => {
        content += `- ${getPriorityIcon(item.priority)} **${item.title}** (ID: \`${item.id}\`)`;
        if (item.description) {
          content += ` - ${item.description}`;
        }
        content += '\n';
      });
      content += '\n';
    }
    
    //  
    if (completed.length > 0) {
      content += `###  \n\n`;
      completed.forEach(item => {
        content += `- ${getPriorityIcon(item.priority)} **${item.title}** (ID: \`${item.id}\`)`;
        if (item.description) {
          content += ` - ${item.description}`;
        }
        if (item.completedAt) {
          content += ` (: ${formatDate(item.completedAt)})`;
        }
        content += '\n';
      });
      content += '\n';
    }
    
    //  
    if (cancelled.length > 0) {
      content += `###  \n\n`;
      cancelled.forEach(item => {
        content += `- ${getPriorityIcon(item.priority)} **${item.title}** (ID: \`${item.id}\`)`;
        if (item.description) {
          content += ` - ${item.description}`;
        }
        content += '\n';
      });
      content += '\n';
    }
    
    content += '\n**   **:\n';
    content += '- `/todo add ` -    \n';
    content += '- `/todo status ID ` -   (pending, in-progress, completed, cancelled)\n';
    content += '- `/todo priority ID ` -   (high, medium, low)\n';
    content += '- `/todo update ID ` -    \n';
    content += '- `/todo delete ID` -   \n';
    
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content
    });
  } catch (error) {
    console.error('      :', error);
    vscode.window.showErrorMessage(`      : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *   
 */
async function addTodo(todoService: TodoService, title: string): Promise<void> {
  try {
    //    ()
    const description = await vscode.window.showInputBox({
      prompt: '    ()',
      placeHolder: '   '
    });
    
    //   (: )
    const priorities = [
      { label: '', value: TodoPriority.HIGH },
      { label: '', value: TodoPriority.MEDIUM, picked: true },
      { label: '', value: TodoPriority.LOW }
    ];
    
    const selectedPriority = await vscode.window.showQuickPick(
      priorities.map(p => p.label),
      { placeHolder: ' ', canPickMany: false }
    );
    
    if (!selectedPriority) {
      return; // 
    }
    
    const priority = priorities.find(p => p.label === selectedPriority)?.value || TodoPriority.MEDIUM;
    
    //   
    const newTodo = todoService.addTodoItem({
      title,
      description,
      priority
    });
    
    //   
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `   "${title}"()  . (ID: \`${newTodo.id}\`, : ${getPriorityLabel(priority)})`
    });
  } catch (error) {
    console.error('     :', error);
    vscode.window.showErrorMessage(`     : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *   
 */
async function updateTodo(todoService: TodoService, todoId: string, newTitle: string): Promise<void> {
  try {
    //   
    const todo = todoService.getTodoItem(todoId);
    
    if (!todo) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `ID \`${todoId}\`      .`
      });
      return;
    }
    
    //    
    todoService.updateTodoItem(todoId, { title: newTitle });
    
    //   
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `   .\n- : "${todo.title}"\n- : "${newTitle}"`
    });
  } catch (error) {
    console.error('     :', error);
    vscode.window.showErrorMessage(`     : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *    
 */
async function changeTodoStatus(todoService: TodoService, todoId: string, statusStr: string): Promise<void> {
  try {
    //   
    const todo = todoService.getTodoItem(todoId);
    
    if (!todo) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `ID \`${todoId}\`      .`
      });
      return;
    }
    
    //    
    let status: TodoStatus;
    
    switch (statusStr.toLowerCase()) {
      case 'pending':
      case 'wait':
      case '':
      case '':
        status = TodoStatus.PENDING;
        break;
        
      case 'in-progress':
      case 'progress':
      case 'doing':
      case '':
      case '':
        status = TodoStatus.IN_PROGRESS;
        break;
        
      case 'completed':
      case 'complete':
      case 'done':
      case '':
      case '':
        status = TodoStatus.COMPLETED;
        break;
        
      case 'cancelled':
      case 'cancel':
      case '':
      case '':
        status = TodoStatus.CANCELLED;
        break;
        
      default:
        await vscode.commands.executeCommand('ape.sendLlmResponse', {
          role: 'assistant',
          content: `  : ${statusStr}.  : pending, in-progress, completed, cancelled`
        });
        return;
    }
    
    //    
    if (todo.status === status) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `  "${todo.title}"()  ${getStatusLabel(status)} .`
      });
      return;
    }
    
    //    
    todoService.changeTodoStatus(todoId, status);
    
    //   
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `  "${todo.title}"  ${getStatusLabel(todo.status)} ${getStatusLabel(status)}() .`
    });
  } catch (error) {
    console.error('      :', error);
    vscode.window.showErrorMessage(`      : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *    
 */
async function changeTodoPriority(todoService: TodoService, todoId: string, priorityStr: string): Promise<void> {
  try {
    //   
    const todo = todoService.getTodoItem(todoId);
    
    if (!todo) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `ID \`${todoId}\`      .`
      });
      return;
    }
    
    //    
    let priority: TodoPriority;
    
    switch (priorityStr.toLowerCase()) {
      case 'high':
      case '':
      case '':
      case 'urgent':
      case '':
        priority = TodoPriority.HIGH;
        break;
        
      case 'medium':
      case 'mid':
      case 'normal':
      case '':
      case '':
        priority = TodoPriority.MEDIUM;
        break;
        
      case 'low':
      case '':
      case '':
        priority = TodoPriority.LOW;
        break;
        
      default:
        await vscode.commands.executeCommand('ape.sendLlmResponse', {
          role: 'assistant',
          content: `  : ${priorityStr}.  : high, medium, low`
        });
        return;
    }
    
    //    
    if (todo.priority === priority) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `  "${todo.title}"()  ${getPriorityLabel(priority)} .`
      });
      return;
    }
    
    //    
    todoService.changeTodoPriority(todoId, priority);
    
    //   
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `  "${todo.title}"  ${getPriorityLabel(todo.priority)} ${getPriorityLabel(priority)}() .`
    });
  } catch (error) {
    console.error('      :', error);
    vscode.window.showErrorMessage(`      : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *   
 */
async function deleteTodo(todoService: TodoService, todoId: string): Promise<void> {
  try {
    //   
    const todo = todoService.getTodoItem(todoId);
    
    if (!todo) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `ID \`${todoId}\`      .`
      });
      return;
    }
    
    //  
    const confirmation = await vscode.window.showWarningMessage(
      `  "${todo.title}"() ?`,
      { modal: true },
      '',
      ''
    );
    
    if (confirmation !== '') {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `   .`
      });
      return;
    }
    
    //   
    const deleted = todoService.deleteTodoItem(todoId);
    
    //   
    if (deleted) {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `  "${todo.title}"() .`
      });
    } else {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `     .`
      });
    }
  } catch (error) {
    console.error('     :', error);
    vscode.window.showErrorMessage(`     : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *    
 */
async function clearAllTodos(todoService: TodoService): Promise<void> {
  try {
    //  
    const confirmation = await vscode.window.showWarningMessage(
      '    ?     .',
      { modal: true },
      '',
      ''
    );
    
    if (confirmation !== '') {
      await vscode.commands.executeCommand('ape.sendLlmResponse', {
        role: 'assistant',
        content: `   .`
      });
      return;
    }
    
    //    
    todoService.clearAllTodos();
    
    //   
    await vscode.commands.executeCommand('ape.sendLlmResponse', {
      role: 'assistant',
      content: `    .`
    });
  } catch (error) {
    console.error('     :', error);
    vscode.window.showErrorMessage(`     : ${error instanceof Error ? error.message : String(error)}`);
  }
}

/**
 *   
 */
function getPriorityLabel(priority: TodoPriority): string {
  switch (priority) {
    case TodoPriority.HIGH: return '';
    case TodoPriority.MEDIUM: return '';
    case TodoPriority.LOW: return '';
    default: return String(priority);
  }
}

/**
 *   
 */
function getStatusLabel(status: TodoStatus): string {
  switch (status) {
    case TodoStatus.PENDING: return '';
    case TodoStatus.IN_PROGRESS: return '';
    case TodoStatus.COMPLETED: return '';
    case TodoStatus.CANCELLED: return '';
    default: return String(status);
  }
}

/**
 *  
 */
function formatDate(date: Date | string | undefined): string {
  if (!date) return '';
  const d = typeof date === 'string' ? new Date(date) : date;
  return d.toLocaleDateString();
}