/**
 *    
 */
export enum TodoPriority {
  HIGH = 'high',
  MEDIUM = 'medium',
  LOW = 'low'
}

/**
 *    
 */
export enum TodoStatus {
  PENDING = 'pending',
  IN_PROGRESS = 'in-progress',
  COMPLETED = 'completed',
  CANCELLED = 'cancelled'
}

/**
 *    
 */
export interface TodoItem {
  id: string;
  title: string;
  description?: string;
  priority: TodoPriority;
  status: TodoStatus;
  createdAt: Date;
  dueDate?: Date;
  updatedAt?: Date;
  completedAt?: Date;
  tags?: string[];
  assignee?: string;
  relatedFiles?: string[];
}

/**
 *    
 */
export interface TodoList {
  items: TodoItem[];
  pendingCount: number;
  completedCount: number;
}

/**
 *     
 */
export interface TodoFilterOptions {
  status?: TodoStatus[];
  priority?: TodoPriority[];
  tags?: string[];
  assignee?: string;
  text?: string;
}

/**
 *     
 */
export enum TodoSortBy {
  PRIORITY = 'priority',
  DUE_DATE = 'dueDate',
  CREATED_AT = 'createdAt',
  UPDATED_AT = 'updatedAt',
  STATUS = 'status',
  TITLE = 'title'
}

/**
 *   
 */
export enum SortDirection {
  ASC = 'asc',
  DESC = 'desc'
}

/**
 *     
 */
export interface TodoSortOptions {
  by: TodoSortBy;
  direction: SortDirection;
}