/**
 *    
 * @param helpData   
 * @param query  
 * @returns LLM  
 */
export function generateHelpSystemPrompt(helpData: any, query: string): string {
  return `
# APE   - LLM  

 APE(Agentic Pipeline Extension)  .           .       .

##  

1.       .
2.      "      ." .
3.    , ,   .
4.    ,     .
5.      .
6.      ,  APE   .

##  

     :

${JSON.stringify(helpData, null, 2)}

##  

: ${query}

##  

    :

1.      H1  H2  
2.       (\`/command\`)
3.     
4.     " "  " "  

 .
`;
}

