/**
 *     
 * @param guideData   
 * @param guideId  ID
 * @returns LLM  
 */
export function generateGuidePrompt(guideData: any[], guideId: string): string {
  const guide = guideData.find(g => g.id === guideId);
  
  if (!guide) {
    return `
# APE   

  (ID: ${guideId})   .     :

${guideData.map(g => `- ${g.id}: ${g.title}`).join('\n')}

  ID   .
`;
  }
  
  return `
# APE   - ${guide.title}

       :

${guide.content}

    :
1.    .
2.    .
3.       .
4.       .

 .
`;
}

